/* General Styles */
.round-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    color: #f3ec99; /* Light yellow text */
    background-color: #5c6f42; /* Main background */
    padding: 20px;
    border-radius: 10px;
  }
  
  /* Header and Footer */
  header {
    background-color: #3d422e; /* Dark green for header */
    color: #f3ec99; /* Light yellow text */
    padding: 15px;
    text-align: center;
    font-size: 24px;
  }
  
  footer {
    background-color: #3d060f; /* Dark maroon for footer */
    color: #f3ec99; /* Light yellow text */
    padding: 10px;
    text-align: center;
    font-size: 14px;
  }
  
  /* Start Round Screen */
  .start-round {
    text-align: center;
  }
  
  .start-button {
    background-color: #212914; /* Dark greenish-black */
    color: #f3ec99; /* Light yellow text */
    padding: 15px 30px;
    font-size: 18px;
    border-radius: 50px;
    margin: 10px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .start-button:hover {
    transform: translateY(-3px);
    background-color: #3d422e; /* Slightly lighter green */
  }
  
  /* Input Field */
  .distance-input {
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    background-color: #212914; /* Dark greenish-black */
    color: #f3ec99; /* Light yellow text */
    width: 200px; /* Set width for consistency */
    margin: 10px 0;
  }
  
  /* Floating Hit Button */
  .floating-hit-button {
    background-color: #212914; /* Dark greenish-black */
    color: #f3ec99; /* Light yellow text */
    border: none;
    border-radius: 50px;
    padding: 15px 30px;
    font-size: 18px;
    cursor: pointer;
    position: relative;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .floating-hit-button:hover {
    transform: translateY(-3px);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.6);
  }
  
  /* Action Buttons */
  .action-buttons {
    display: flex;
    gap: 10px;
  }
  
  .curved-button {
    border-radius: 50px;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    background-color: #212914; /* Dark greenish-black */
    color: #f3ec99; /* Light yellow text */
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .curved-button:hover {
    transform: translateY(-2px);
    opacity: 0.9;
  }
  
  .hit-button {
    background-color: #212914; /* Dark greenish-black */
  }
  
  .drop-button {
    background-color: #3d060f; /* Dark maroon */
    color: #f3ec99; /* Light yellow text */
  }
  
  /* Next Hole Button */
  .next-hole-button {
    margin-top: 20px;
    background-color: #212914; /* Dark greenish-black */
    color: #f3ec99; /* Light yellow text */
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: transform 0.2s ease, background-color 0.3s ease;
  }
  
  .next-hole-button:hover {
    transform: translateY(-2px);
    background-color: #3d422e; /* Slightly brighter dark green */
  }
  
  /* Progress Bar */
  .progress-bar {
    display: flex;
    gap: 5px;
    margin-top: 10px;
  }
  
  .progress-step {
    width: 20px;
    height: 20px;
    background-color: #f3ec99; /* Light yellow */
    border: 2px solid #212914; /* Dark greenish-black */
    border-radius: 50%;
    opacity: 0.3;
    transition: opacity 0.3s, transform 0.3s;
  }
  
  .progress-step.completed {
    opacity: 1;
    background-color: #212914; /* Dark greenish-black */
    transform: scale(1.2);
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .fadeIn {
    animation: fadeIn 0.5s ease-in-out;
  }
  
  /* Final Score Display */
  .final-score {
    font-size: 24px;
    font-weight: bold;
    color: #f3ec99; /* Light yellow */
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .distance-input {
      width: 150px;
    }
    .start-button {
      font-size: 16px;
      padding: 10px 20px;
    }
    .floating-hit-button {
      font-size: 16px;
      padding: 12px 24px;
    }
  }
  
  
  

  

  
  